                  item.key_orders.discharge_orders
                );
                this.stage[index].key_orders.long_term_orders = this.repeat(
                  item.key_orders.long_term_orders
                );
                this.stage[index].key_orders.notification = this.repeat(
                  item.key_orders.notification
                );
                this.stage[index].key_orders.temporary_orders = this.repeat(
                  item.key_orders.temporary_orders
                );
              });
              // this.stage = this.repeat(res.data.stage);
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    repeat(arr) {
      let str = "";
      // console.log(arr);
      if (arr != "") {
        if (arr.length != 0) {
          arr.forEach((item, index) => {
            // console.log(item);
            // item 为空就不执行
            if (item != "") {
              if (index == 0) {
                // 数组里只有一项的时候 就只返回一个
                str = index + 1 + "^" + item;
                // console.log(str);
              } else {
                str = str + "\n" + (index + 1) + "^" + item;
                // console.log(str);
              }
            }
          });
          return str;
        }
      } else {
        return str;
      }
    },
    timeUnit() {
      this.stage.forEach((item, index) => {
        // console.log(item);
        // console.log(item.currentUnit);
        item.currentUnit = this.standard_entry_days;
      });
      // this.stageItem.currentUnit = this.standard_entry_days;
    },
    btn() {
      var stagg = JSON.stringify(this.stage);
      let formName = this.form_name;
      let cpId = this.cp_id;
      if (formName == "" || cpId == "") {
        this.$alert("务必填写名字以及ID号", "请注意", {
          confirmButtonText: "确定",
          callback: action => {
            this.$message({
              type: "info",
              message: "已取消提交"
            });
          }
        });
      } else {
        this.$confirm("请确认是否提交信息？", "确认信息", {
          distinguishCancelAndClose: true,
          confirmButtonText: "确定提交",
          cancelButtonText: "放弃提交"
        })
          .then(() => {
            this.$axios
              .get("http://192.168.50.78:8001/operationExamFormData", {
                params: {
                  cp_id: this.cp_id,
                  form_manager: this.form_manager,
                  form_name: this.form_name,
                  suitable_subject_disc: this.suitable_subject_disc,
                  standard_entry_days: this.standard_entry_days,
                  timeMin: this.timeMin,
                  timeMax: this.timeMax,
                  severity_level: this.severity_level,
                  stage: stagg
                }
              })
              .then(res => {
                this.$message({
                  message: "数据提交成功",
                  type: "success"
                });
                window.location.reload();
                // console.log("成功了");
              })
              .catch(err => {
                this.$message.error("数据提交失败~");
                console.log(err);
              });
          })
          .catch(action => {
            this.$message("数据未提交");
          });
      }
      // console.log(this.stage);
    }
  }
};
</script>

<style scoped>
.el-header {
  background-color: #ffffff;
  min-height: 180px;
  color: #333;
  text-align: center;
}
.el-main {
  overflow: auto;
  background-color: #e9eef3;
  color: #333;
  text-align: center;
}
.el-row {
  margin-bottom: 20px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  border-radius: 4px;
  text-align: left;
  background: #99a9bf;
}
.input1 {
  width: 90%;
  height: 42px;
  margin-left: 22px;
  border: none;
  outline: none;
  border-bottom: 1px solid #9e9e9e;
  text-align: center;
}
input[type="text"]:focus {
  border-bottom: 1px solid #0d47a1;
  box-shadow: 0 1px 0 0 #0d47a1;
}
.container {
  padding: 10px 0px;
  width: 100%;
  height: 100%;
}
.scene-container {
  width: 33.333%;
  border-radius: 2px;
  background-color: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  float: left;
  box-sizing: border-box;
  padding: 10px 0px;
  min-height: 100px;
}
.table {
  width: 100%;
  min-height: 100%;
  border: 1px solid red;
  display: flex;
  box-sizing: border-box;
}
.table-sun {
  width: 30px;
  writing-mode: vertical-lr;
  letter-spacing: 5px;
  font-size: 20px;
  padding: 0px 10px;
  display: inline-block;
  border-right: 1px solid green;
}
.textarea {
  resize: none;
  width: 98%;
  height: 100%;
  border: none;
  outline: none;
}
.el-main::-webkit-scrollbar {
  width: 3px;
}
.el-main::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  border-radius: 1px;
}

.el-main::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.4);
}
</style>



